.card-cover {
  min-height: 360px;
  position: relative;
  perspective: 1000px;
}

.card {
  transform-style: preserve-3d;
  transition: transform 1s ease;
  position: absolute;
  width: 100%;
  height: 100%;

  cursor: pointer;
}

.card-front,
.card-back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  transform: rotateX(0deg)
}

.card-front {
  z-index: 5;
  background-color: darkslateblue;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1rem 0;
}

.card-back {
  z-index: 4;
  transform: rotateY(180deg);
  background-color: rgb(156, 70, 70);
  padding: 1rem;
}

.card.flip {
  transform: rotateY(180deg);
}

.card-image-box {
  flex: 1;
  position: relative;
  width: 100%;
}

.card-image {
  position: absolute;
  width: 100%;
  height: 100%;
}

.card-image > img {
  /* width: 100%; */
  /* height: 100%; */
  max-width: 100%;
  max-height: 100%;
}
